/* Home.css */

body {
    font-family: Arial, sans-serif;
    background: #E0F2FE; /* Lighter Blue */
    color: #111827; /* Neutral Blue */
  }
  
  .section-container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
    margin: 20px;
    background: white;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    opacity: 0;
    transform: translateY(20px);
    transition: opacity 0.6s ease-out, transform 0.6s ease-out;
  }
  
  .section-container:hover {
    transform: translate(10px, 10px); /* Move down to the right on hover */
    box-shadow: 0 12px 24px rgba(0, 0, 0, 0.15); /* Add a shadow effect on hover */
    transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out; /* Smooth transition for the hover effect */
  }
  
  .section-image {
    width: 150px;
    border-radius: 8px;
    margin-right: 20px;
  }
  
  .section-text {
    flex-grow: 1;
    max-width: calc(100% - 220px); /* Adjust based on image size and desired space */
  }
  
  .section-text p {
    line-height: 1.6;
  }
  
  /* Title Styling */
  .section-text h2,
  .section-text h3 {
    color: #1E3A8A; /* Primary Blue */
    font-weight: bold;
  }
  
  /* Specific Styles for Projects Section */
  .projects-section {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  
  .projects-section .section-image {
    width: 45%;
    margin: 10px;
  }
  
  .projects-section .project-link {
    display: block;
    width: 48%;
  }
  
  .projects-section .project-image-2 {
    width: 100%;
    height: auto; /* Ensure the height adjusts proportionally */
    border-radius: 8px;
    transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out; /* Smooth transition for hover effect */
  }
  
  .projects-section .project-image-2:hover {
    transform: scale(1.05); /* Slightly enlarge image on hover */
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2); /* Add shadow effect on hover */
  }
  
  .projects-section .project-image-2:last-child {
    margin-right: 0; /* Remove margin for the last image */
  }
  
  /* Responsive Design */
  @media (max-width: 768px) {
    .section-container {
      flex-direction: column;
      align-items: flex-start;
    }
  
    .projects-section {
      flex-direction: column;
      align-items: center;
    }
  
    .projects-section .section-image {
      width: 90%;
      margin: 10px 0;
    }
  
    .projects-section .project-link {
      width: 90%;
      margin-bottom: 20px;
    }
  
    .projects-section .project-image-2 {
      width: 90%;
      max-height: none;
    }
  }
  